<template>
  <AbstarctContent />
</template>

<script>
import { db } from "@/firebase";
import { setDoc, doc, updateDoc, serverTimestamp } from "firebase/firestore";
import { v4 as uuidv4 } from "uuid";
import AbstarctContent from "./components/AbstractContent.vue";
import axios from "axios";

export default {
  name: "App",
  data() {
    return {
      id: "",
      location_url:"",
      locationData: null
    };
  },
  methods: {
    async updateData() {
      await updateDoc(doc(db, "dm", this.id), {
        end: serverTimestamp()
      });
    },
    async getData() {
      if (this.locationData == null) {
        


        if (localStorage.getItem("data") == null) {

          
         await axios.get("https://api.ipify.org/?format=json").then(response => {
            this.location_url =
              "https://ipapi.co/" + response.data["ip"] + "/json/";
            
          });

          await axios.get(this.location_url).then(response => {
              this.locationData = response.data;
              localStorage.setItem("data", JSON.stringify(this.locationData));
              this.id = uuidv4();
              localStorage.setItem("uuid", this.id.toString());
              
            });


        } else {
          

          this.locationData = JSON.parse(localStorage.getItem("data"));
          this.id = localStorage.getItem("uuid").toString();

          
        }
      }

      await setDoc(doc(db, "dm", this.id), {
        begin: serverTimestamp(),
        end: serverTimestamp(),
        ip : this.locationData["ip"],
        city: this.locationData["city"],
        state: this.locationData["region"]
      });

      setInterval(this.updateData, 10000);
    }
  },
  created() {
    this.getData();
  },
  components: {
    AbstarctContent
  }
};
</script>

